import { useEffect, useState } from "react";
import Release from "./Release";
import LoadingGif from "../loading.gif";

function Releases() {
  const [isLoading, setIsLoading] = useState(true);
  const [loadedReleases, setLoadedReleases] = useState([]);

  useEffect(() => {
    setIsLoading(true);
    fetch("https://portal-dev.k3imagine.com/ps-jiratools/release-dates")
      .then((response) => {
        return response.json();
      })
      .then((data) => {
        let releases = [];
        console.log(data);
        data.values.forEach((release) => {
          let rcRelease = { ...release };
          let releaseDate = new Date(release.releaseDate);
          if (releaseDate.getMonth() === 10 && releaseDate.getDate() > 20) {
            release.lastReleaseOfYear = true;
            release.year = releaseDate.getFullYear();
          }
          let nextRCDate = new Date(release.releaseDate);
          nextRCDate.setDate(nextRCDate.getDate() - 7);
          nextRCDate = formatDate(nextRCDate);
          rcRelease.userReleaseDate = nextRCDate;
          rcRelease.type = "rc";
          releases.push(rcRelease);
          release.userReleaseDate = formatDate(releaseDate);
          release.type = "prod";
          // release.userReleaseDate = formatDate(nextRCDate);
          releases.push(release);
        });
        setIsLoading(false);
        setLoadedReleases(releases);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  function getOrdinal(d) {
    if (d > 3 && d < 21) return "th";
    switch (d % 10) {
      case 1:
        return "st";
      case 2:
        return "nd";
      case 3:
        return "rd";
      default:
        return "th";
    }
  }

  function getMonthName(m) {
    switch (m) {
      case 0:
        return "January";
      case 1:
        return "February";
      case 2:
        return "March";
      case 3:
        return "April";
      case 4:
        return "May";
      case 5:
        return "June";
      case 6:
        return "July";
      case 7:
        return "August";
      case 8:
        return "September";
      case 9:
        return "October";
      case 10:
        return "November";
      default:
        return "December";
    }
  }

  function formatDate(d) {
    const ordinal = getOrdinal(d.getDate());
    const month = getMonthName(d.getMonth());
    const formattedDate = `${d.getDate()}${ordinal} ${month} ${d.getFullYear()}`;
    return formattedDate;
  }

  if (isLoading) {
    return (
      <div className="center">
        <img className="loading" src={LoadingGif} alt="loading" />
      </div>
    );
  }

  return (
    <div className="timeline" id="content">
      {loadedReleases.map((release) => (
        <Release key={release.id} release={release} />
      ))}
    </div>
  );
}

export default Releases;
