import React from "react";

function Release(props) {
  return (
    <div
      className={`container ${
        props.release.type === "prod" ? "right" : "left"
      }`}
    >
      <div className="content">
        <h2 className={props.release.type}>
          {props.release.type === "prod" ? "Production" : "RC"} Release
        </h2>
        <h3>
          {props.release.type === "prod" ? "Evening of " : ""}{" "}
          {props.release.userReleaseDate}
        </h3>
        {props.release.type === "rc" && (
          <p>Production Preview, UAT and Customer Access</p>
        )}
        {props.release.lastReleaseOfYear && (
          <p>Last release of {props.release.year}</p>
        )}
      </div>
    </div>
  );
}

export default Release;
